<script>
import $ from "jquery";
export default {
  data() {
    return {
      users: [],
      tempUser: {},
      // message: "",
      isNew: false,
      filter: "",
      apiUrl: "http://140.112.30.202:3000",
    };
  },
  mounted() {
    // let vm = this;
    // this.axios.get(`${this.apiUrl}/users`)
    // .then(res=>{
    //   vm.users = res.data;
    //    //vm.addData =res.data.addData
    // }).catch(err=>{
    //   alert(`資料連線失敗 ${err}，請聯絡服務人員`)
    // })
  },
  computed: {
    filtered_User: function () {
      let vm = this;
      return this.users
        .filter((item) => {
          let tag = ["userName", "userID", "userEmail", "addData"];
          let flag = false;
          tag.forEach(function (now_tag) {
            console.log(typeof item);
            if (
              String(item[now_tag])
                .toLocaleLowerCase()
                .indexOf(vm.filter.toLocaleLowerCase()) != -1
            ) {
              flag = true;
            }
          });
          return flag;
        })
        .map(function (item) {
          let temp_item = JSON.parse(JSON.stringify(item));
          if (vm.filter == "" && vm.filter == null) return temp_item;
          temp_item.userName.replace(
            vm.filter,
            "<span id='highlight'>" + vm.filter + "</span>"
          );
          return temp_item;
        });
    },
  },
  methods: {
    getUser() {
      let vm = this;
      this.axios
        .get(`${this.apiUrl}/users`)
        .then((res) => {
          vm.users = res.data;
          //vm.addData =res.data.addData
        })
        .catch((err) => {
          alert(`資料連線失敗 ${err}，請聯絡服務人員`);
        });
    },
    openModel(isNew, item) {
      if (isNew) {
        this.tempUser = {};
        this.isNew = true;
      } else {
        this.tempUser = Object.assign({}, item);
        this.isNew = false;
      }
      $("#userModal").modal("show");
    },
    openDeleteModal(item) {
      $("#deleteModal").modal("show");
      this.tempUser = Object.assign({}, item);
    },
    deletUser() {
      const vm = this;
      this.axios.delete(`${vm.apiUrl}/users/${vm.tempUser.userID}`)
      .then(res=>{
        console.log(res)
        let data = res.data;
        $("#deleteModal").modal("hide");
        this.$bus.$emit(
          "message:push",
          `${data.message},${vm.tempUser.userName}`,
          "danger"
        );
        this.getUser();
      }).catch(err=>{
        console.log(err)
      })
    },
    updateUser() {
      this.$validator.validate().then((result) => {
        if (result) {
          //console.log(result)
          const vm = this;
          let api = `${vm.apiUrl}/users`;
          let httpMethod = "post";
          // console.log(api);
          if (!vm.isNew) {
            api = `${vm.apiUrl}/users/${vm.tempUser.userID}`;
            httpMethod = "put";
          }
          this.$http[httpMethod](api, vm.tempUser).then((response) => {
            let data = response.data;
            console.log(data);
            if (Object) {
              console.log(data.message);
              $("#userModal").modal("hide");
              this.getUser();
              this.$bus.$emit(
                "message:push",
                `${data.message}  ,${vm.tempUser.userName}`,
                "success"
              );
            } else {
              console.log(data.message);
              $("#userModal").modal("show");
              this.getUser();
              this.$bus.$emit(
                "message:push",
                `${data.message}  ,${vm.tempUser.userName}`,
                "danger"
              );
              //console.log(response.data);
            }
          });
        } else {
          console.log(data.message);
          $("#userModal").modal("show");
          this.getUser();
          this.$bus.$emit(
            "message:push",
            `${data.message} , ${vm.tempUser.userName}`,
            "danger"
          );
        }
      });
    },
  },
  created() {
    this.getUser();
  },
};
</script>
