export default {
  name: "SimpleTable",
  components: {
    // MySelect,
  },
  data() {
    return {
      walk: [],
      tempwalk: {},
      isNew: false,
      filter: "",
      apiUrl: "http://140.112.30.202:3000",
      text: "台灣尋寶王",
      // pagination
      // pageIndex: 1,
      // goToPage: "",
      //一頁10個資料
      //總資料數量
      // dataListLength: "",
      // pageSize: 20,
      // pageSizeList: [20, 30, 50, 100],
      // showOption: false,
      // townIndex: 0,
      // areaIndex: 0,
    };
  },
  methods: {
    getWalks() {
      const vm = this;
      //console.log(api);
      this.axios
        .get(`${this.apiUrl}/walkPlaceList`)
        .then((res) => {
          // console.log(res.data);
          vm.walk = res.data;
          vm.dataListLength = res.data.length; //typeof number
          // vm.pageTotalNum = Math.ceil(vm.dataListLength / this.pageSize);
          // console.log(vm.dataListLength); //230
          // console.log(vm.pageTotalNum); //12
        })
        .catch((err) => {
          alert(`連線錯誤，請連線服務人員${err}`);
        });
    },
    // add & Edite Model
    openModel(isNew, item) {
      // console.log(isNew, item);
      if (isNew) {
        this.tempwalk = {};
        this.isNew = true;
      } else {
        this.tempwalk = Object.assign({}, item);
        this.isNew = false;
      }
      $("#walkModal").modal("show");
    },
    //delete Model
    openDeleteModal(item) {
      $("#deleteModal").modal("show");
      this.tempwalk = Object.assign({}, item);
    },
    deleteWalk() {
      const vm = this;
      let api = `${vm.apiUrl}/walkPlaceList/${vm.tempwalk.walkId}`;
      this.axios.delete(api).then((response) => {
        //console.log(response.data.message, vm.tempwalk);
        let data = response.data;
        $("#deleteModal").modal("hide");
        this.$bus.$emit(
          "message:push",
          `${data.message} , ${vm.tempwalk.walkPlaceName}`,
          "danger"
        );
        this.getWalks();
      });
    },
    //post & put methods
    updateWalk() {
      this.$validator.validate().then((result) => {
        if (result) {
          const vm = this;
          let api = `${vm.apiUrl}/walkPlaceList`;
          let httpMethod = "post";
          // console.log(api);
          if (!vm.isNew) {
            api = `${vm.apiUrl}/walkPlaceList/${vm.tempwalk.walkId}`;
            httpMethod = "put";
          }
          this.$http[httpMethod](api, vm.tempwalk).then((response) => {
            let data = response.data;
            if (Object) {
              $("#walkModal").modal("hide");
              vm.getWalks();
              // console.log(response.data);
              this.$bus.$emit(
                "message:push",
                `${data.message}  ,  ${vm.tempwalk.walkPlaceName}`,
                "success"
              );
            } else {
              vm.getWalks();
              this.$bus.$emit(
                "message:push",
                `${data.message}  ,  ${vm.tempwalk.walkPlaceName}`,
                "danger"
              );
              //console.log(response.data);
            }
          });
        } else {
          this.$bus.$emit(
            "message:push",
            `${data.message} , ${vm.tempwalk.walkPlaceName}`,
            "danger"
          );
        }
      });
    },
   
    
    //pagination
    // prevOrNext: function (n) {
    //   this.pageIndex += n;
    //   this.pageIndex <= 1
    //     ? (this.pageIndex = 1)
    //     : this.pageIndex > this.pageTotalNum
    //     ? (this.pageIndex = this.pageTotalNum)
    //     : null;
    // },
    // selectPage: function (n) {
    //   if (n === this.pageIndex) return;
    //   if (typeof n === "string") return;
    //   this.pageIndex = n;
    //   console.log(typeof this.pageIndex); //number
    // },
    // handleGoToPage: function() {
    //   this.pageIndex = this.goToPage <= 1 ? 1 : this.goToPage - 0 >= this.pageTotalNum - 0 ? this.pageTotalNum :
    //   this.goToPage
    //   this.goToPage = this.pageIndex
    // },
  },
  computed: {
    filtered_Walk: function () {
      let vm = this;
      return this.walk
        .filter((item) => {
          let tag = [
            "walkId",
            "walkPlaceName",
            "walkCity",
            "walkAddress",
            "walkArea",
          ];
          let flag = false;
          tag.forEach(function (now_tag) {
            // console.log(typeof item);
            if (
              String(item[now_tag])
                .toLocaleLowerCase()
                .indexOf(vm.filter.toLocaleLowerCase()) != -1
            ) {
              flag = true;
            }
          });
          return flag;
        })
        .map(function (item) {
          let temp_item = JSON.parse(JSON.stringify(item));
          if (vm.filter === "" ) return temp_item;
          if(temp_item.walkPlaceName.replace === null) return temp_item;
            temp_item.walkPlaceName.replace(
              vm.filter,
              "<span id='highlight'>" + vm.filter + "</span>"
            );
          return temp_item;
        });
    },
    pageTotalNum: function () {
      return this.dataListLength % this.pageSize == 0
        ? this.dataListLength / this.pageSize
        : Math.floor(this.dataListLength / this.pageSize) + 1;
    },
    // pages: function () {
