<script>
import Alert from "./Alert.vue";
import moment from "moment";
export default {
  name: "TopNavbar",
  components: {
    Alert,
  },
  data() {
    return {
      selectedEmployee: null,
      nowDate: new Date().getTime(),
      userName: "",
      userPic: "",
      apiUrl: "http://140.112.30.202:3000",
    };
  },
  methods: {
    toggleSidebar() {
      this.$sidebar.displaySidebar(!this.$sidebar.showSidebar);
    },
    getUserInfo() {
      this.userName = window.localStorage.getItem("userName");
      this.userPic = window.localStorage.getItem("userPic");
    },
    signout() {
      const vm = this;
      vm.$http
        .get(`${vm.apiUrl}/api/logout`)
        .then((res) => {
          //console.log(res);
          if(res.data.message === "登出成功")
            vm.$router.push({path: '/login'})
            localStorage.clear();
            this.$bus.$emit(
            "message:push",
            `${res.data.message}`,
            "danger"
          );
        })
        .catch((err) => {
          alert(`連線錯誤，請連線服務人員${err}`);
        });
    },
  },
  created() {
    this.nowDate = moment(this.nowDate).format("YYYY年MM月DD日, 時間 HH:mm:ss");
    this.getUserInfo();
  },
};
</script>

