<template>
  <div>
    <!-- <div
      v-if="img"
      :style="{ backgroundImage: 'url(' + loginImg + ')', backgroundSize: cover }"
    ></div> -->
    <img class="login-img" src="@/assets/img/login.jpg" />
    <form class="form-signin" method="post" @submit.prevent="signin">
      <h3 class="text-center">台灣尋寶王 後台管理系統</h3>
      <h5 class="text-center">請先登入</h5>
      <!-- <label for="inputEmail" class="sr-only">Email address</label> -->
      <input
        type="email"
        id="inputEmail"
        class="form-control text-center"
        placeholder="請輸入管理者帳號 (super@gmail.com)"
        ref="myInput"
        v-model.trim="userEmail"
        required
      />
      <!-- <label for="inputPassword" class="sr-only">Password</label> -->
      <input
        type="password"
        id="inputPassword"
        class="form-control text-center"
        v-model.trim="userPassword"
        placeholder="請輸入管理者密碼 (123456)"
        required
      />
      <button
        class="btn btn-lg btn-info btn-block"
        type="sumbit"
        @keyup.enter="signin"
      >
        登入
      </button>
    </form>
  </div>
</template>
<script>
import loginImg from "../assets/img/login.jpg";
export default {
  name: "Login",
  data() {
    return {
      loginImg,
      userEmail: "",
      userPassword: "",
      apiUrl: "http://140.112.30.202:3000",
    };
  },
  methods: {
    async signin() {
      let params = new URLSearchParams();
      params.append("userEmail", this.userEmail);
      params.append("userPassword", this.userPassword);

      const api = `${this.apiUrl}/api/authenticate`;
      if (this.userEmail === "" || this.userPassword === "") {
        alert("帳號密碼不能為空");
      } else {
        this.$http.post(api, params, {
            headers: {
              token: this.userEmail,
            },
          })
          .then((res) => {
            let data = res.data;
            if (data.status === true) {
              localStorage.clear();
              localStorage.setItem("token", data.token);
              localStorage.setItem("userPic", data.results[0].userPic);
              localStorage.setItem("userName", data.results[0].userName)
              // console.log(data)
              // console.log(`token ${data.token} --- userPic ${data.results[0].userPic}--- userName  ${data.results[0].userName}`);
              this.$router.push("/admin/dashboard");
            } else if (res.data.status === false && res.data.message === "信箱或密碼不符合") {
              localStorage.clear();
              alert(res.data.message);
              // console.log(res.data.message)
            }else{
              // console.log(res.data.message)
              localStorage.clear();
              alert(res.data.message);
              //this.$router.push("/reigster");
            }
          })
          .catch((error) => {
            console.error(error);
          })
      }
    },
  },
  computed: {},
  //進入頁面input游標自動到設定的input中
  mounted() {
    this.$nextTick(() => {
      this.$refs.myInput.focus();
    });
  },
};
</script>

<style scoped>
img {
  /* z-index: -1; */
  width: 100%;
  position: relative;
  top: 0%;
  left: 0%;
}
form {
  position: absolute;
  background-color: rgb(255, 255, 255, 0.8);
  top: 20%;
  right: 0%;
  /* bottom:0%; */
  left: 0%;
  margin: auto;
  border-radius: 3px;
}
.form-signin {
  width: 100%;
  max-width: 330px;
  padding: 15px;
  margin: auto;
}
.form-signin .checkbox {
  font-weight: 400;
}
.form-signin .form-control {
  position: relative;
  box-sizing: border-box;
  height: auto;
  padding: 10px;
  font-size: 16px;
}
.form-signin .form-control:focus {
  z-index: 2;
}
.form-signin input[type="email"] {
  margin-bottom: -1px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}
.form-signin input[type="password"] {
  margin-bottom: 10px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}
h3 {
  font-weight: bolder;
}
h5 {
  font-weight: 700;
}
button:hover {
}
</style>